.main-groups {
    margin: 10px 12px;
    padding: 0;
    list-style: none;
}

.main-group {
    border: 1px solid black;
    border-radius: 5px;
    margin: 10px auto;
    width: 80%;
    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.5);
}

.main-group__title {
    text-align: center;
    font-family: inherit;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

.main-group__links {
    margin: 0;
    padding: 0;
    list-style: none;
}

.main-group__links li {
    margin: 4px 12px;
    padding: 1.5px 3px;
}

.Form {
    width: 100%;
}

.Form__Row {
    width: 100%;
    margin: 10px auto;
}

.Btn {
    height: 30px;
    min-width: 40px;
    cursor: pointer;
    border: 1px solid black;
    border-radius: 3px;
    box-shadow: 2px 2px 2px black;
}

.Btn__Save {
    background-color: #c5c5c5;
}

.Input {
    padding: 10px;
}
* {
    box-sizing: border-box;
}

.app {
    background: #f5f5f5;
    border: 1px solid black;
    border-radius: 3px;
    box-sizing: content-box;
}

.Links {
    display: inline-block;
    width: 50%;
    border-right: 1px solid black;
    box-sizing: border-box;
}

.Form {
    display: inline-block;
    width: 50%;
    vertical-align: top;
    box-sizing: border-box;
    text-align: center;
}

.main-title {
    text-align: center;
}
